import * as React from "react"
import TextBody from "../components/text_body"
import Group from "../components/group"
import TextList from "../components/text_list"
import TextImage from "../components/text_image"
import CustomImage from "./custom_image"
import CustomVideo from "./custom_video"

const imageStyle = {
    display: "flex",
    alignItems: "stretch"
}
export default function ContentSwitcher(item, narrowWindow, isGroup) {
    switch (item.type) {
        case "TextBody":
            return <TextBody 
                        title={item.title} 
                        body={item.body} 
                        links={item.links} 
                        narrowWindow={narrowWindow}
                    />
        case "TextImage":
            return <TextImage 
                        title={item.title} 
                        imageName={item.imageName} 
                        alt={item.alt} 
                        narrowWindow={narrowWindow}
                    />    
        case "Group":
            return <Group 
                        content={item.content} 
                        narrowWindow={narrowWindow}
                    />
        case "TextList":
            return <TextList 
                        title={item.title} 
                        items={item.items}
                    />
        case "Image":
            return <CustomImage 
                        imageName={item.imageName} 
                        alt={item.alt} 
                        style={imageStyle}
                        objectFit={isGroup ? "cover" : "contain"}
                    />
        case "Video":
            return <CustomVideo 
                        videoId={item.videoId} 
                        videoTitle={item.title}
                    />
        default:
            return <p>{item.type}</p>
    }
}